









import { Component, Vue } from 'vue-property-decorator';

import { LanguageService } from '@/lang/translation';

@Component
export default class LanguagePicker extends Vue {
  private langs: string[] = [];

  private mounted() {
    this.langs = [ LanguageService.Instance.lang, ...[ 'pl', 'hu', 'en' ].filter(l => l !== LanguageService.Instance.lang) ].filter(l => Boolean(l));
  }

  private change(lang: string) {
    LanguageService.Instance.change(lang);
  }
}
