export enum LayoutBoxSize {

}

export interface LayoutParamters {
  backgrounds: Background[];
}

const width = 12;
const height = 12;

class BoxTemplate {
  constructor(public count: number, public width: number, public height: number) {

  }
}

const boxTemplates: BoxTemplate[] = [
  new BoxTemplate(6, 4, 4),
  new BoxTemplate(8, 3, 3),
  new BoxTemplate(10, 2, 2),
  new BoxTemplate(16, 1, 1),
]

class Box {
  public get xP(): string { return ( this.x / width * 100) + '%'; }
  public get yP(): string { return ( this.y / height * 100) + '%'; }
  public get wP(): string { return ( this.width / width * 100) + '%'; }
  public get hP(): string { return ( this.height / height * 100) + '%'; }

  constructor(public x: number, public y: number, public width: number, public height: number, public background: Background, public blur: number, public opacity: number) {

  }
}

export class Layout {
  constructor(public boxes: Box[]) {

  }
}

function random(min: number, max: number) {
    const range = max - min;
    return Math.floor(Math.random() * range) + min; 
  }
  
function generateBox(template: BoxTemplate, backgrounds: Background[], blur: number, opacity: number): Box {
  const x = random(0, width - template.width + 1);
  const y = random(0, height - template.height + 1);
  const background = backgrounds[random(0, backgrounds.length )];

  return new Box(x, y, template.width, template.height, background, blur, opacity);
}

export interface Background {
    color?: string;
    image?: string;
}

export function generateLayout(parameters?: LayoutParamters, optimization = 1.0): Layout {
  const boxes = [];
  let level = 0;

  for(const box of boxTemplates) {
    const desiredBoxes = (box.count * optimization);
    for(let index = 0; index < desiredBoxes; index++) {
      boxes.push(generateBox(box, parameters?.backgrounds ?? [ { color: '#000000' } ], 12 - (level * 4), level / 4));
    }
    level++;
  }
  
  return new Layout(boxes);
}

export function shuffle(layout: Layout): Layout {
  for(const box of layout.boxes) {
    box.x = random(0, width - box.width + 1);
    box.y = random(0, height - box.height + 1); 
  }

  return layout;
}

export default generateLayout;