import { MenuTileData } from '@/model/menu.model';
import colors from '@/data/colors';
import { translate, LanguageService } from '@/lang/translation';
import thumbnails from '@/data/thumbnails';

export default function get(lang?: string): MenuTileData[] {
  lang = lang ?? LanguageService.Instance.lang;

  return [
    {
      color: colors.projectColor,
      label: translate('project', lang),
      thumbnail: thumbnails.get('menu/project.jpg'),
      items: [
        { 
          href: '#about',
          name: translate('about', lang),
        }
      ]
    },
    {
      color: colors.nikiforColor,
      label: translate('nikifor', lang),
      thumbnail: thumbnails.get('menu/nikifor.jpg'),
      items: [
        { 
          href: '#n-bio',
          name: translate('artist', lang),
        },
        { 
          href: '#n-museum',
          name: translate('museum', lang),
        },
        { 
          href: '#n-map',
          name: translate('map', lang),
        },
        { 
          href: '#n-region',
          name: translate('region', lang),
        },
        { 
          href: '#n-ex',
          name: translate('exhibition', lang),
        },
        { 
          href: '#n-catalog',
          name: translate('catalog', lang),
        }
      ]
    },
    {
      color: colors.violaColor,
      label: translate('viola', lang),
      thumbnail: thumbnails.get('menu/berki.jpg'),
      items: [
        { 
          href: '#v-bio',
          name: translate('artist', lang),
        },
        { 
          href: '#v-museum',
          name: translate('museum', lang),
        },
        { 
          href: '#v-map',
          name: translate('map', lang),
        },
        { 
          href: '#v-region',
          name: translate('region', lang),
        },
        { 
          href: '#v-ex',
          name: translate('exhibition', lang),
        },
        { 
          href: '#v-catalog',
          name: translate('catalog', lang),
        }
      ]
    },
  ]
}