














import { Component, Vue } from 'vue-property-decorator';
import AppHeader from './components/AppHeader.vue';
import AppFooter from './components/AppFooter.vue';
import Layout from './components/Layout.vue';
import Menu from './components/Menu.vue';
import MobileMenu from './components/MobileMenu.vue';
import Fund from '@/components/Fund.vue';
import Museum from '@/components/Museum.vue';

import About from '@/components/areas/About.vue';
import Artist from '@/components/areas/Artist.vue';
import { ArtistData } from './model/artist.model';
import artists from '@/data/artists';

@Component({
  components: {
    About,
    Artist,
    AppHeader,
    AppFooter,
    Fund,
    Layout,
    Menu,
    MobileMenu,
    Museum,
  },
})
export default class App extends Vue {  
  private get artists(): ArtistData[] | null { return artists(this.$lang.lang); }
}
