










import { Component } from 'vue-property-decorator';
import DataContext from '@/components/DataContext.vue';
import DataSection from '@/components/DataSection.vue';
import Audio from '@/components/Audio.vue';
import audioUrlProvider from '@/data/audio';

import BaseArea from './BaseArea.vue';

@Component({
  components: {
    Audio,
    DataContext,
    DataSection,
  },
})
export default class About extends BaseArea {
  public getAudioUrl(lang: string): string { 
    return audioUrlProvider(lang); 
  } 
}
