import generate, { Layout } from '@/helper/layout.generator';
import thumbnails from '@/data/thumbnails';

export default  function generateLayout(optimizationRatio = 1.0): Layout {
  return generate({
  backgrounds: [ 
    { color: '#2f063e'}, 
    { color: '#44002a'}, 
    { color: '#024232'}, 
    { color: '#024232', image: thumbnails.get('nikifor/1.jpg') }, 
    { color: '#024232', image: thumbnails.get('nikifor/2.jpg') }, 
    { color: '#024232', image: thumbnails.get('nikifor/3.jpg') }, 
    { color: '#024232', image: thumbnails.get('nikifor/4.jpg') }, 
    { color: '#024232', image: thumbnails.get('nikifor/5.jpg') }, 
    { color: '#024232', image: thumbnails.get('nikifor/6.jpg') }, 
    { color: '#44002a', image: thumbnails.get('berki/1.jpg') }, 
    { color: '#44002a', image: thumbnails.get('berki/7.jpg') }, 
    { color: '#44002a', image: thumbnails.get('berki/27.jpg') }, 
    { color: '#44002a', image: thumbnails.get('berki/29.jpg') }, 
    { color: '#44002a', image: thumbnails.get('berki/38.jpg') }, 
    { color: '#44002a', image: thumbnails.get('berki/40.jpg') }, 
  ]}, optimizationRatio);
}