



















import { Component, Prop, Vue } from 'vue-property-decorator';
import { GalleryImage } from '../../model/artist.model';

@Component({
  components: {    
  },
})
export default class Gallery extends Vue {
  @Prop() images!: GalleryImage[];
  public current: GalleryImage | null = null;

  private show(image: GalleryImage) {
    this.current = image;
    
    this.$nextTick(() => {
      (this.$refs.previewImage as Element)?.scrollIntoView({ inline: 'start' });
    });
  }
}
