













import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class YTVideo extends Vue {
  @Prop() private url!: string;
  public watching = false;
  public load = false;

  private onFrameLoad(): void {
    this.load = true;
  }

  private watch(): void {
    this.watching = true;
  }
}
