import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import './theme/theme.scss'
import './resize'
import './translate'
import './zoom'

import AOS from 'aos'
import 'aos/dist/aos.css'

Vue.config.productionTip = false

new Vue({
  created: () => AOS.init(),
  render: h => h(App),
}).$mount('#app')

declare module 'vue/types/vue' {
  interface Vue {
    $t: (resource: string, lang: string) => string;
    $lang: { lang: string };
  } 
}