








import { Component, Vue } from 'vue-property-decorator';
import { Layout, shuffle } from './../helper/layout.generator';
import generateLayout from '@/data/layout';

@Component
export default class AppLayout extends Vue {
  private layout: Layout | null = null;
  private isAdornerOpaque = false;

  mounted() {    
    const simplified = this.$screen.width <= 768;
    this.layout = generateLayout(simplified ? 0.25 : 0.5);

    if (!simplified) {
      setInterval(() => {
        if(this.layout) {
          this.isAdornerOpaque = !this.isAdornerOpaque;
          this.layout = shuffle(this.layout);
        }
      }, 10000);
    }   
  }
}
