import pl from '@/lang/pl.json';
import hu from '@/lang/hu.json';
import en from '@/lang/en.json';
import { LangDictionary } from './LangDictionary';

export class LanguageService {
  private changeCallbacks = new Array<(lang: string) => void>();

  public static Instance: LanguageService;

  public lang: string;

  constructor(lang: string) {
    this.lang = lang;
  }

  public change(lang: string) {
    this.lang = lang;
    this.changeCallbacks?.forEach(c => c(lang));
  }

  public onchange(callback: (lang: string) => void) {
    this.changeCallbacks.push(callback);
  }
} 

// eslint-disable-next-line
const language = (window.navigator as any)?.userLanguage || window.navigator.language;
const ln = (language as string)?.substr(0, 2);

LanguageService.Instance = new LanguageService(ln ?? 'pl');

export function translate(resource: string, lang?: string): string {
  if(lang === 'pl') {
    return (pl as LangDictionary)[resource] as string;
  }

  if(lang === 'hu') {
    return (hu as LangDictionary)[resource] as string;
  }

  if(lang === 'en') {
    return (en as LangDictionary)[resource] as string;
  }

  return resource;
}
