








import { Component, Vue, Watch } from 'vue-property-decorator';
import MobileMenuTile from '@/components/MobileMenuTile.vue';
import { MenuTileData } from '@/model/menu.model';
import menuItems from '@/data/menuItems';

@Component({
  components: {
    MobileMenuTile,
  },
})
export default class MobileMenu extends Vue {
  private menuItems: MenuTileData[];

  constructor() {
    super();

    this.menuItems = menuItems();
  }
  
  @Watch("$lang.lang")
  onLangChanged(newLang: string) {
    this.menuItems = menuItems(newLang);
  }
}
